'use client';

import React, { createContext, useContext, useState, useEffect } from 'react';
import { roadmapData } from '@/data/roadmap';

interface SubTask {
  id: string;
  title: string;
  completed: boolean;
  estimatedHours: number;
  actualHours?: number;
  resources?: string[];
}

interface Milestone {
  id: string;
  title: string;
  description: string;
  status: 'Completed' | 'In Progress' | 'Not Started';
  technologies: string[];
  github?: string;
  month: string;
  projectId: string;
  subTasks: SubTask[];
  startDate?: Date;
  endDate?: Date;
  totalHours?: number;
  completedHours?: number;
  resources: string[];
  achievements: string[];
}

interface MilestoneStats {
  completed: number;
  inProgress: number;
  notStarted: number;
  total: number;
  completedPercentage: number;
  totalHours: number;
  completedHours: number;
  averageCompletionTime: number;
  achievements: string[];
  resources: string[];
}

interface MilestoneContextType {
  milestones: Milestone[];
  stats: MilestoneStats;
  updateMilestoneStatus: (id: string, status: 'Completed' | 'In Progress' | 'Not Started') => void;
  updateSubTask: (milestoneId: string, subTaskId: string, completed: boolean) => void;
  addSubTask: (milestoneId: string, subTask: SubTask) => void;
  addResource: (milestoneId: string, resource: string) => void;
  addAchievement: (milestoneId: string, achievement: string) => void;
  updateTimeTracking: (milestoneId: string, hours: number) => void;
}

const MilestoneContext = createContext<MilestoneContextType | undefined>(undefined);

export function MilestoneProvider({ children }: { children: React.ReactNode }) {
  const [milestones, setMilestones] = useState<Milestone[]>(() => {
    // Initialize with default sub-tasks and resources
    return roadmapData.map(item => ({
      ...item,
      subTasks: getDefaultSubTasks(item.id),
      resources: getDefaultResources(item.id),
      achievements: [],
      totalHours: 0,
      completedHours: 0,
    }));
  });

  const stats = calculateStats(milestones);

  useEffect(() => {
    // Load saved data from localStorage
    const savedMilestones = localStorage.getItem('milestones');
    if (savedMilestones) {
      try {
        const parsed = JSON.parse(savedMilestones);
        setMilestones(parsed);
      } catch (error) {
        console.error('Error loading milestones:', error);
      }
    }
  }, []);

  useEffect(() => {
    // Save to localStorage whenever milestones change
    localStorage.setItem('milestones', JSON.stringify(milestones));
  }, [milestones]);

  const updateMilestoneStatus = (id: string, status: 'Completed' | 'In Progress' | 'Not Started') => {
    setMilestones(prev => prev.map(milestone => 
      milestone.id === id ? { ...milestone, status } : milestone
    ));
  };

  const updateSubTask = (milestoneId: string, subTaskId: string, completed: boolean) => {
    setMilestones(prev => prev.map(milestone => {
      if (milestone.id === milestoneId) {
        const updatedSubTasks = milestone.subTasks.map(task =>
          task.id === subTaskId ? { ...task, completed } : task
        );
        const allCompleted = updatedSubTasks.every(task => task.completed);
        return {
          ...milestone,
          subTasks: updatedSubTasks,
          status: allCompleted ? 'Completed' : milestone.status
        };
      }
      return milestone;
    }));
  };

  const addSubTask = (milestoneId: string, subTask: SubTask) => {
    setMilestones(prev => prev.map(milestone =>
      milestone.id === milestoneId
        ? { ...milestone, subTasks: [...milestone.subTasks, subTask] }
        : milestone
    ));
  };

  const addResource = (milestoneId: string, resource: string) => {
    setMilestones(prev => prev.map(milestone =>
      milestone.id === milestoneId
        ? { ...milestone, resources: [...milestone.resources, resource] }
        : milestone
    ));
  };

  const addAchievement = (milestoneId: string, achievement: string) => {
    setMilestones(prev => prev.map(milestone =>
      milestone.id === milestoneId
        ? { ...milestone, achievements: [...milestone.achievements, achievement] }
        : milestone
    ));
  };

  const updateTimeTracking = (milestoneId: string, hours: number) => {
    setMilestones(prev => prev.map(milestone =>
      milestone.id === milestoneId
        ? { ...milestone, completedHours: (milestone.completedHours || 0) + hours }
        : milestone
    ));
  };

  return (
    <MilestoneContext.Provider value={{
      milestones,
      stats,
      updateMilestoneStatus,
      updateSubTask,
      addSubTask,
      addResource,
      addAchievement,
      updateTimeTracking,
    }}>
      {children}
    </MilestoneContext.Provider>
  );
}

function getDefaultSubTasks(milestoneId: string): SubTask[] {
  const defaultTasks: Record<string, SubTask[]> = {
    'python-mastery': [
      { id: 'python-basics', title: 'Python Basics & Syntax', completed: false, estimatedHours: 20 },
      { id: 'oop', title: 'Object-Oriented Programming', completed: false, estimatedHours: 15 },
      { id: 'file-handling', title: 'File Handling & I/O', completed: false, estimatedHours: 10 },
      { id: 'web-scraping', title: 'Web Scraping & APIs', completed: false, estimatedHours: 15 },
    ],
    'ml-basics': [
      { id: 'data-preprocessing', title: 'Data Preprocessing', completed: false, estimatedHours: 15 },
      { id: 'model-training', title: 'Model Training & Evaluation', completed: false, estimatedHours: 20 },
      { id: 'feature-engineering', title: 'Feature Engineering', completed: false, estimatedHours: 15 },
    ],
    // Add default tasks for other milestones
  };

  return defaultTasks[milestoneId] || [];
}

function getDefaultResources(milestoneId: string): string[] {
  const defaultResources: Record<string, string[]> = {
    'python-mastery': [
      'Python Official Documentation',
      'Real Python Tutorials',
      'Python Crash Course Book',
    ],
    'ml-basics': [
      'Scikit-learn Documentation',
      'Hands-on Machine Learning Book',
      'Kaggle Courses',
    ],
    // Add default resources for other milestones
  };

  return defaultResources[milestoneId] || [];
}

function calculateStats(milestones: Milestone[]): MilestoneStats {
  const completed = milestones.filter(m => m.status === 'Completed').length;
  const inProgress = milestones.filter(m => m.status === 'In Progress').length;
  const notStarted = milestones.filter(m => m.status === 'Not Started').length;
  const total = milestones.length;
  const completedPercentage = Math.round((completed / total) * 100);

  const totalHours = milestones.reduce((sum, m) => sum + (m.totalHours || 0), 0);
  const completedHours = milestones.reduce((sum, m) => sum + (m.completedHours || 0), 0);

  const allAchievements = milestones.flatMap(m => m.achievements);
  const allResources = milestones.flatMap(m => m.resources);

  return {
    completed,
    inProgress,
    notStarted,
    total,
    completedPercentage,
    totalHours,
    completedHours,
    averageCompletionTime: completed > 0 ? completedHours / completed : 0,
    achievements: [...new Set(allAchievements)],
    resources: [...new Set(allResources)],
  };
}

export function useMilestones() {
  const context = useContext(MilestoneContext);
  if (context === undefined) {
    throw new Error('useMilestones must be used within a MilestoneProvider');
  }
  return context;
} 